.collab
{
    text-align: right;
}

.collab p
{
    font-weight: lighter !important;
    margin-bottom: 20px;
}

.hr
{
    background-color: rgba(255, 255, 255, 0.2);
    height: 1px;
}

.info h4
{
    font-size: 18px;
    font-weight: lighter;
}

.info p
{
    color: grey;
    font-weight: lighter;
}

.info li
{
    font-weight: lighter;
    color: #fff;
    font-size: 18px;
    padding-left: 20px;
}

#fb::before
{
    display: inline-block;
    content: '';
    border-radius: 100%;
    height: 4px;
    width: 4px;
    margin-right: 6px;
    background: #3B5998;
}
#ig::before
{
    display: inline-block;
    content: '';
    border-radius: 100%;
    height: 4px;
    width: 4px;
    margin-right: 6px;
    background: #FCAF45;
}
#tw::before
{
    display: inline-block;
