#media, #address
{
    text-align: right;
}

#media ul
{
    list-style: none;
}

#media ul li
{
    display: inline-block;
}

@media(max-width: 768px)
{
    .collab, #personal, #media, #address
    {
        text-align: center;
    }

    .info ul
    {
        margin: 0 0 0 -22px;
        padding: 0;
    }
}