html, body
{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: "Roboto Mono" !important;
    background-color: #0f0f0f !important;
    color: #fff !important;
}

::-webkit-scrollbar{
    background: transparent;
    width: 10px;
}

::-webkit-scrollbar-thumb{
    background: linear-gradient(180deg, rgba(122,122,122,1) 0%, rgba(106,106,106,1) 0%, rgba(127,127,127,1) 46%, rgba(150,150,150,1) 100%);
    border-radius: 10px;
}

.line
{
    width: 24em;
    top: 50%;
    margin: auto;
    border-right: 2px solid rgba(255, 255, 255, 0.75);
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    transform: translateY(-50%);
}

.anim-typewriter
{
    animation: typewriter 4s steps(30) 1s 1 normal both,
    blinkTextCussor 500ms steps(30) infinite normal;
}

@keyframes typewriter
{
    from
    {
        width: 0;
    }
    to
    {
        width: 17em;
    }
}

@media(max-width: 768px)
{
    @keyframes typewriter
    {
        from
        {
            width: 0;
        }
        to
        {
            width: 16em;
        }
    }
}

@keyframes blinkTextCussor
{
    from
    {
        border-right-color: rgba(255, 255, 255, 0.75);
    }
    to
    {
        border-right-color: transparent;
    }
}