.hero
{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero h1
{
    font-weight: lighter;
    text-align: center;
    letter-spacing: -2px;
    line-height: 58px;
}

@media (max-width: 768px)
{
    .header h1
    {
        font-size: 24px;
    }
}
@media (max-width: 360px)
{
    .header h1
    {
        font-size: 18px;
    }
}

/*---------------- WORKS ----------------------*/

.hero-content
{
    margin-left: 0%;
}

.hero-content p 
{
    font-weight: 300;
    line-height: 36px;
    color: grey;
}

.hero-content h6
{
    text-align: left;
}

@media(max-width: 768px)
{
    .hero-content
    {
        padding-top: 50px;
        width: 96%;
        margin: 0 auto;
    }
}