h6
{
    text-align: center;
}

.vertical
{
    margin-top: 40px;
    border-left: 1px solid #fff;
    height: 80px;
    position: absolute;
    left: 50%;
}

.project
{
    height: 900px;
}

.project1
{
    background: url(../../../build/img/bg1.webp) no-repeat 50% 50%;
    background-size: cover;
}

.project1::after
{
    content: 'safeless way.';
    font-family: "Roboto Mono";
    font-size: 36px;
    position: relative;
    color: #fff;
    display: block;
    top: 8%;
    left: -35%;
    right: 0;
    bottom: 0;
}

.project2
{
    background: url(../../../build/img/bg2.webp) no-repeat 50% 50%;
    background-size: cover;
}

.project2::after
{
    content: 'freezetime.';
    font-family: "Roboto Mono";
    font-size: 36px;
    position: relative;
    color: #fff;
    display: block;
    top: 80%;
    left: 90%;
    right: 0;
    bottom: 0;
}

.project3
{
    background: url(../../../build/img/bg3.webp) no-repeat 50% 50%;
    background-size: cover;
}

.project3::after
{
    content: 'collapse.';
    font-family: "Roboto Mono";
    font-size: 36px;
    position: relative;
    color: #fff;
    display: block;
    top: 92%;
    left: -23%;
    right: 0;
    bottom: 0;
}

.project4
{
    background: url(../../../build/img/bg4.webp) no-repeat 50% 50%;
    background-size: cover;
}

.project4::after
{
    content: 'remember it.';
    font-family: "Roboto Mono";
    font-size: 36px;
    position: relative;
    color: #fff;
    display: block;
    top: 10%;
    left: 90%;
    right: 0;
    bottom: 0;
}

@media (max-width: 768px)
{
    .project
    {
        width: 90% !important;
        margin: 14px auto;
    }

    .vertical
    {
        display: none;
    }

    .project1::after, .project2::after, .project3::after, .project4::after
    {
        display: none;
    }
}